.navigation_button
    position: relative
    flex-grow: 1
    flex-basis: 15%
    cursor: pointer
    // margin: 16px 30px
    border: none
    transition: 2s
    overflow: visible

    display: flex
    align-items: center
    justify-content: center

    &::before
        opacity: 0
        content: ""
        display: block
        position: absolute
        width: 80%
        height: 5vh
        border-radius: 20px 0
        z-index: 1
        background-image: linear-gradient(90deg, var(--mainColor1), var(--mainColor2), var(--mainColor3))
        background-size: 100vw 100%
        background-position-x: -400%
        transition: .3s linear
        box-shadow: 0 0 20px rgba(0, 0, 0, 0.1)

    &--active
        &::before
            opacity: 1
            transform: skew(4deg, 1.5deg)

    &--upper_level
        flex-grow: 0

    &--first
        &::before
            background-position-x: 0%

    &--second
        &::before
            background-position-x: 25%

    &--third
        &::before
            background-position-x: 50%

    &--fourth
        &::before
            background-position-x: 75%

    &--fifth
        &::before
            background-position-x: 100%

    &__button
        position: absolute
        padding: 0
        width: 80%
        height: 5vh
        color: white
        font-size: var(--fontSizeBigger)
        border: none
        background-color: transparent
        border-radius: 18px 0
        cursor: pointer
        overflow: hidden
        display: flex

        justify-self: center
        justify-content: center
        align-items: center

        &::before
            position: absolute
            top: 0
            left: 0
            z-index: 2
            width: 100%
            height: 5vh
            content: ""
            display: block
            transition: .2s linear
            border-radius: 18px 0
            background-image: radial-gradient(circle at center, var(--mediumGray), var(--darkerGray))
            background-clip: padding-box
            border: 1px solid transparent

        &::after
            position: absolute
            z-index: 3
            top: -20vh
            left: 50%
            transform: translateX(-50%)
            width: 0%
            height: 0%
            border-radius: 50%
            content: ""
            display: block
            transition: .5s
            background-image: linear-gradient(90deg, var(--mainColor1), var(--mainColor2), var(--mainColor3))
            background-size: 100vw 100%

        &--first
            &::after
                background-position-x: 0%

        &--second
            &::after
                background-position-x: 25%

        &--third
            &::after
                background-position-x: 50%

        &--fourth
            &::after
                background-position-x: 75%

        &--fifth
            &::after
                background-position-x: 100%

        &:hover
            span
                color: var(--almostWhite)
            &::after
                height: 40vh
                width: 40vh

        &--upper_level
            flex-grow: 0
            background-image: linear-gradient(90deg, var(--mainColor1), var(--mainColor2), var(--mainColor3))
            background-clip: text
            color: transparent
            background-size: 100vw 100%
            &:hover
                span
                    color: var(--almostWhite)
            &::after, &::before
                opacity: 0

        &_text
            position: absolute
            display: block
            z-index: 30
            background-image: linear-gradient(90deg, var(--mainColor1), var(--mainColor2), var(--mainColor3))
            background-clip: text
            color: transparent
            background-size: 100vw 100%
            background-position-x: -400%
            font-weight: bold
            transition: .5s
            text-transform: uppercase

            &--first
                background-position-x: 0%
            &--second
                background-position-x: 25%
            &--third
                background-position-x: 50%
            &--fourth
                background-position-x: 75%
            &--fifth
                background-position-x: 100%
            &--hidden
                opacity: 0
