.gallery_image
    grid-row: span 2
    grid-column: span 2
    position: relative

    &--normal
        &:nth-child(1)
            grid-row: span 4
            grid-column: span 4

        &:nth-child(5)
            grid-row: span 2
            grid-column: span 4

        &:nth-child(8)
            grid-row: span 4
            grid-column: span 4

    &--miniature
        grid-row: span 1
        grid-column: span 1
        // aspect-ratio: 1 / 1
        // &::before
        //     content: ""
        //     display: block
        //     padding-top: 100%

    &--enlarged
        width: 100%
        height: 100%

    &__imag
        position: absolute
        top: 0
        left: 0
        width: 100%
        height: 100%
        object-fit: cover
        border-radius: var(--borderRadiusSmall)
        filter: grayscale(100%) brightness(80%) contrast(110%)
        cursor: pointer
        transition: 0.4s
        transform: scale(1)

        &:hover
            z-index: 99
            filter: grayscale(0%)
            transform: scale(1.1)

        &--active
            filter: grayscale(0%)
            cursor: default
            &:hover
                transform: scale(1)
